*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-main: "DM Sans", sans-serif;
  --font-accent: "Titan One", cursive;
  --font-size: 16px;
  --text-color-main: #000;
  --text-color-accent: #907e82;
  --text-color-light: #fff;
  --primary-color: #d41443;
  --accent-color: #ffa5ba;
  --btn-primary-color: #d41342;
  --btn-accent-color: #ecebeb;
  --btn-hero-color: #f15277;
  --bg-color: #ffb8ca;
  --bg-menu-color: #e17992;
  --line-menu-color: #e18298;
  --icon-bg-color: #fea5bb;
  --bg-color-light: #fff5f6;
  --bg-color-shadow: rgba(255, 184, 202, .2);
  --product-color-first: #ffb8ca;
  --product-color-second: #f0d1a5;
  --product-color-third: #c2e297;
  --card-shadow: 0px 5px 10px 5px rgba(34, 60, 80, .2), 0px -5px 10px -5px rgba(34, 60, 80, .2);
  --gutter: 32px;
  --gutter-mob: 20px;
  --section-padding: 60px;
  --transition-prop: .25s ease;
}

body {
  font-family: var(--font-main);
  font-size: var(--font-size);
  color: var(--text-color-main);
}

body.modal-open {
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visual-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

a {
  text-decoration: none;
}

.container {
  margin: 0 auto;
}

@media screen and (max-width: 767.9px) {
  .container {
    max-width: 480px;
    padding-left: var(--gutter-mob);
    padding-right: var(--gutter-mob);
  }
}

@media screen and (min-width: 768px) and (max-width: 1199.9px) {
  .container {
    width: 768px;
    padding-left: var(--gutter);
    padding-right: var(--gutter);
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: calc(var(--gutter) / 2);
    padding-right: calc(var(--gutter) / 2);
  }
}

.no-scroll {
  overflow: hidden;
}

.section {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
}

.section__title {
  font-family: var(--font-accent);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--primary-color);
  margin-bottom: 20px;
  font-size: 34px;
  line-height: 1.15;
}

@media screen and (min-width: 768px) and (max-width: 1199.9px) {
  .section__title {
    font-size: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .section__title {
    font-size: 58px;
    line-height: 1.14;
  }
}

.section__subtitle {
  font-family: var(--font-accent);
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--accent-color);
  margin-bottom: 16px;
  font-size: 18px;
  line-height: 1.16;
}

@media screen and (min-width: 768px) and (max-width: 1199.9px) {
  .section__subtitle {
    font-size: 26px;
    line-height: 1.15;
  }
}

@media screen and (min-width: 1200px) {
  .section__subtitle {
    font-size: 30px;
    line-height: 1.13;
  }
}

@keyframes scale-up-center {
  0% {
    opacity: 0;
    transform: scale(.5);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes shadow-drop-center {
  0% {
    box-shadow: 0 0 rgba(255, 255, 255, 0);
  }

  100% {
    box-shadow: 0 0 15px 1px var(--primary-color);
  }
}

@keyframes gallery-scale {
  0% {
    z-index: 2;
    transform: scale(1);
  }

  5% {
    z-index: 2;
    transform: scale(1.2);
  }

  10% {
    z-index: 2;
    transform: scale(1);
  }

  11% {
    z-index: 1;
    transform: scale(1);
  }

  100% {
    z-index: 1;
    transform: scale(1);
  }
}

@keyframes heartbeat {
  0% {
    transform: scale(1.07);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.07);
  }
}

@keyframes rolling {
  0% {
    transform: translateX(0) rotate(0);
  }

  50% {
    transform: translateX(70px)rotate(360deg);
  }

  100% {
    transform: translateX(0) rotate(0);
  }
}

.btn {
  min-width: 161px;
  min-height: 44px;
  cursor: pointer;
  font-weight: 700;
  font-size: var(--font-size);
  color: var(--text-color-light);
  background-color: var(--btn-primary-color);
  transition: all var(--transition-prop);
  border: 0;
  border-radius: 22px;
  padding: 12px 17px;
  line-height: 1.31;
  display: block;
  box-shadow: 0 0 20px -15px #000;
}

@media screen and (min-width: 768px) and (max-width: 1199.9px) {
  .btn {
    min-width: 168px;
    padding-left: 35px;
    padding-right: 35px;
  }
}

.btn:focus, .btn:hover {
  color: var(--primary-color);
  background-color: #fff;
  transform: scale(1.05);
  box-shadow: 0 0 20px -10px #000;
}

.btn--with-icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn--wide {
  min-width: 176px;
}

.btn--sm {
  min-width: 62px;
  min-height: 24px;
  border-radius: 20px;
  padding: 8px 14px;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
}

@media screen and (min-width: 1200px) {
  .btn--sm {
    padding: 11px 22px;
    font-size: 14px;
    line-height: 1.29;
  }
}

.btn--sm:focus, .btn--sm:hover {
  transform: scale(1.1);
}

.btn--reverse {
  color: var(--primary-color);
  background-color: #fff;
}

.btn--reverse:focus, .btn--reverse:hover {
  background-color: var(--btn-accent-color);
}

.btn--centered {
  margin-left: auto;
  margin-right: auto;
}

.btn--animated {
  animation-name: shadow-drop-center;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.btn-round {
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: all var(--transition-prop);
  border: 0;
  border-radius: 50%;
  padding: 0;
  box-shadow: 0 0 20px -15px #000;
}

.btn-round:focus, .btn-round:hover {
  color: var(--primary-color);
  background-color: var(--btn-accent-color);
  transform: scale(1.05);
  box-shadow: 0 0 20px -10px #000;
}

.btn-round--sm {
  width: 18px;
  height: 18px;
}

@media screen and (min-width: 1200px) {
  .btn-round--sm {
    width: 26px;
    height: 26px;
  }
}

.btn__icon {
  stroke: currentColor;
  fill: currentColor;
}

.btn__icon--right {
  margin-left: 8px;
}

@media screen and (min-width: 768px) {
  .btn__icon--right {
    margin-left: 13px;
  }
}

.btn__icon--left {
  margin-right: 8px;
}

@media screen and (min-width: 768px) {
  .btn__icon--left {
    margin-right: 13px;
  }
}

.btn__icon--rotate {
  transform: rotate(0);
  transition: transform var(--transition-prop);
}

.btn:hover .btn__icon--rotate, .btn:focus .btn__icon--rotate, .btn-round:hover .btn__icon--rotate, .btn-round:focus .btn__icon--rotate {
  transform: rotate(-180deg);
}

.btn-round--sm .btn__icon {
  width: 7px;
  height: 7px;
}

@media screen and (min-width: 1200px) {
  .btn-round--sm .btn__icon {
    width: 9px;
    height: 9px;
  }
}

